.textAndPicture {
  display: flex;
  flex-direction: row;
  align-items: center;
  overflow-x: hidden;
  width:100%;
  @include mobile {
    flex-direction: column;
  }
  .left {
    display: flex;
    flex: 2;
    overflow: hidden;
    margin: 0 20px;
    justify-content: center;
    align-items: flex-center;
    height: 400px;
    width: 100%;
    .img-box {
      display: flex;
      align-items: flex-end;
      margin: 20px 0;
        height: 400px;
        width: 600px;
        overflow-y: hidden;
      @include mobile {
        height: 400px;
        margin-top: 0;
        padding: 0 20px;
      }
      img {
        width: 100%;
      }
    }
  }
  .right {
    flex: 3;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: left;
    row-gap: 20px;
    @include mobile {
      //align-items: center;
    }
    p {
      //font-size: 18px;
      padding: 0 20px;
    }
    .horizList {
      flex-direction: row;
      padding: 0 20px;
      .title {
        //font-weight: 500;
      }
      p {
        //font-size: 18px;
        padding: 0;
      }
    }
    .links {
      display: flex;
      flex-direction: row;
      align-content: center;
      padding: 0 20px;
      column-gap: 20px;
      .icon {
        font-size: 30px;
      }
      a {
        //font-size: 30px;
        text-decoration: none;
        color: $topBarLinkColor;
        //margin: 0 10px;
      }
      a:hover {
        color: $linkHColor;
      }
    }
  }
}